.acf-block-preview .cta-strip {
  pointer-events: none;
}

.cta-strip {
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.cta-strip .cta_strip {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: flex-start;
  -ms-flex-align: flex-start;
  -webkit-align-items: flex-start;
  align-items: flex-start;
  -webkit-justify-content: flex-start;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -moz-flex-direction: column;
  -webkit-flex-direction: column;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #1779ba;
  padding: 40px 50px;
  width: 100%;
  min-width: 300px;
}
.cta-strip .cta_strip h3 {
  color: #fefefe;
}
.cta-strip .cta_strip p {
  margin: 0;
  color: #fefefe;
}
.cta-strip .cta_strip-controls {
  margin-left: 0;
  margin-top: 20px;
}
.cta-strip .cta_strip-controls .button {
  font-size: 2rem;
}

@media print, screen and (min-width: 64em) {
  .cta-strip {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  .cta-strip .cta_strip {
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    -webkit-justify-content: space-between;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -moz-flex-direction: row;
    -webkit-flex-direction: row;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
  }
  .cta-strip .cta_strip-controls {
    margin-left: 40px;
    margin-top: 0;
  }
  .cta-strip .cta_strip-controls .button {
    font-size: 2.625rem;
    min-width: 275px;
  }
  .cta-strip .cta_strip p {
    font-size: 1.25rem;
  }
}